.review__label--control {
  position: relative;
  display: block;
  padding: 10px 0 10px 62px;
}
.review__label--control:hover .review__control-mark {
  background-color: #e5e5e5;
}
.review__label--control:active .review__control-mark {
  background-color: #d5d5d5;
}
.review__label--control:active .review__input[type=radio]:checked + .review__control-mark::before {
  opacity: 0.3;
}

.review__control-mark {
  position: absolute;
  top: 0;
  left: 0;
  width: 46px;
  height: 46px;
  border: none;
  background-color: #f2f2f2;
}

.review__input[type=checkbox]:checked + .review__control-mark::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 46px;
  height: 46px;
  background-image: url("../../img/tick.svg");
  background-position: center;
  background-repeat: no-repeat;
  z-index: 1;
}

.review__input[type=radio] + .review__control-mark {
  border-radius: 50%;
}

